<template>
  <div class="registrationConfirmation" style="text-align:center; width:270px; margin:auto">
    <div style="height:36px" />
    <span class="p12">{{ title }}</span>
    <div style="height:20px" />
    <span class="p9">{{ message }}</span>
    <div style="height:38px" />
    <span class="btn" :class="this.$root.theme_bc_1" style="width:100%;" v-on:click="this.$root.pushHome"></span>
    <br>
    <br>
    <br>
    <br>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'RegistrationConfirmation',
  components: {
  },
  data() {
    return {
      title : "",
      message : "",
    };
  },
  computed : {
  },
  methods : {
    initAction : function () {
      let tempId = this.$route.params.tempId;
      
      if (tempId == null || tempId.length != 20) {
        this.title = "";
        this.message = "URL";
        return;
      }

      let params = new URLSearchParams();
      params.append('tempId', tempId);

      axios.post(this.$root.SERVER_PATH + 'register', params)
      .then((response) => {
        let resData = response.data;
        if (resData["status"] == this.$root.API_FAILED_NODATA || resData["status"] == this.$root.API_FAILED_PARAM) {
          //
          this.title = "";
          this.message = "URL";
          this.$root.closePopup();
          return;
        }
        if (resData["status"] == this.$root.API_FAILED_DUPLICATE) {
          //
          this.title = "";
          this.message = "";
          this.$root.closePopup();
          return;
        }
        if (resData["status"] == this.$root.API_FAILED_EXEPTION) {
          //
          this.title = "";
          this.message = "";
          this.$root.closePopup();
          return;
        }

        if (resData["kbn"] == "NEW") {
          if (!this.$root.checkServerResponse(response, false)) {
            return;
          }

          this.title = "";
          this.message = "<br>";
        } else if (resData["kbn"] == "ADD") {
          if (!this.$root.checkServerResponse(response, false)) {
            return;
          }

          this.title = "";
          this.message = "<br>";
          this.$root.closePopup();
        } else if (resData["kbn"] == "CHANGE") {
          if (!this.$root.checkServerResponse(response, false)) {
            return;
          }

          this.title = "";
          this.message = "<br>";
        }

        this.$root.closePopup();
      }).catch((error) => {
        this.$root.comNetworkError(error);
      });
    },
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.initAction();
      vm.$root.setCurrentPage("");
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.initAction();
      vm.$root.setCurrentPage("");
    });
  }
}
</script>

